<script>
    export let color = '0, 0, 0,';
    let canvas;
    let windowWidth;
    let windowHeight = window.innerHeight;

    $: if (canvas || windowWidth) {
        generatePixels();
    }

    function generatePixels() {
        let pixelsWidth = canvas ? canvas.clientWidth : 0;
        const ctx = canvas.getContext('2d');
        const grid = 200; // pixels in each row
        const pW = Math.floor(pixelsWidth / grid) + 1;
        const pH = pW;
        const rows = Math.floor((windowHeight * 3) / pH); // number of rows

        // adjust canvas height to fit all rows
        const totalHeight = rows * pH;
        canvas.width = pixelsWidth;
        canvas.height = totalHeight;

        ctx.clearRect(0, 0, canvas.width, canvas.height);

        // draw each pixel as a filled rectangle
        for (let i = 0; i < rows; i++) {
            for (let j = 0; j < grid; j++) {

                let randomScale = Math.random() * (-0.2 + (0.4 * (i / (rows - 1))));
                let randomExtraJuice = (Math.random() - 1) / 10
                let randomFactor = (randomScale - randomExtraJuice);

                let baseOpacity = i / rows;
                let finalOpacity = Math.min(1, Math.max(0, baseOpacity + randomFactor));

                ctx.fillStyle = `rgba(${color} ${finalOpacity.toFixed(2)})`;
                ctx.fillRect(j * pW, i * pH, pW, pH);
            }
        }
    }
</script>

<div class="canvas-container">
    <canvas
        id="pixelCanvas"
        bind:this={canvas}
        bind:clientWidth={windowWidth}
    ></canvas>
</div>

<style>
    canvas {
        width: 100%;
        display: block;
    }
    
    .canvas-container {
        height: 100%; 
    }
</style>
