<script>
    import { narrativeStore, scrollPositionStore } from "../../lib/stores.js";
    import { onMount } from 'svelte';
    export let backgroundY;

    const absoluteBackgroundY = $scrollPositionStore + backgroundY;
    $: relativeY = $scrollPositionStore - absoluteBackgroundY;

    const canvasWidth = 500;
    const canvasHeight = 500;
    const spriteWidth = 500;
    const spriteHeight = 500;

    let canvas;
    let ctx;
    let animationId;
    let positionX = 50;

    let frameX = 0;
    let frameCount = 0;
    const staggerFrames = 25;

    const balloonImage = new Image();
    $: balloonImage.src = `img/balloonImages_${$narrativeStore}.png`;

    function cancelAnimation() {
        if (animationId) {
            cancelAnimationFrame(animationId); 
        }
    }

    function startAnimation() {
        function animate() {
            ctx.clearRect(0, 0, canvasWidth, canvasHeight);
            ctx.drawImage(
                balloonImage,
                frameX * spriteWidth, 0, spriteWidth, spriteHeight,
                0, 0, spriteWidth, spriteHeight
            );

            frameCount++;
            if (frameCount % staggerFrames === 0) { // reset at end of loop
                frameX = frameX < 3 ? frameX + 1 : 0;
            }
            if (frameCount > staggerFrames * 4) { // reset framecount to stop infinite climb
                frameCount = 0;
            }

            animationId = requestAnimationFrame(animate);
        }
        animate();
    }

    onMount(() => {
        ctx = canvas.getContext('2d');
        canvas.width = canvasWidth;
        canvas.height = canvasHeight;
        frameCount = 0;

        balloonImage.onload = function () {
            cancelAnimation();  // cancel the previous animation
            startAnimation();   // start a new animation
        };
    });

    $: if (relativeY < 2700) {
        positionX = 50;
    } else if (relativeY >= 2700 && relativeY < 4000) {
        positionX = 80;
    } else if (relativeY >= 4000 && relativeY < 6000) {
        positionX = 20;
    } else if (relativeY >= 6000 && relativeY < 9500) {
        positionX = 50;
    } else if (relativeY >= 9500 && relativeY < 12500) {
        positionX = 100;
    } else if (relativeY >= 12500) {
        positionX = 50;
    }

</script>

<canvas
    id="canvas1"
    bind:this={canvas}
    style="left: {positionX}%"
    >
</canvas>

<style>
    #canvas1 {
        transition: all 1s ease-in-out;
        border: 0;
        background-color: transparent;
        position: sticky;
        top: 20%;
        transform: translate(-50%, 0%);
        width: 120px;
        height: 120px;
        z-index: 1000;
    }
</style>
