<script>
    import { narrativeStore } from "../../lib/stores.js";

    function handleIdChange(id) {
        narrativeStore.set(id);
    }
</script>

<div class="button-container">
<button
    id="narrative01-button"
    class="narrative-choice geo-regular"
    class:selected={$narrativeStore === 'narrative1'}
    on:click={() => handleIdChange('narrative1')}
    on:keydown={(e) => e.key === 'Enter' && handleIdChange('narrative1')}
    >Windy stuff</button>

<button
    id="narrative02-button"
    class="narrative-choice geo-regular"
    class:selected={$narrativeStore === 'narrative2'}
    on:click={() => handleIdChange('narrative2')}
    on:keydown={(e) => e.key === 'Enter' && handleIdChange('narrative2')}
    >Solar panels</button>

<button
    id="narrative03-button"
    class="narrative-choice geo-regular"
    class:selected={$narrativeStore === 'narrative3'}
    on:click={() => handleIdChange('narrative3')}
    on:keydown={(e) => e.key === 'Enter' && handleIdChange('narrative3')}
    >Electric vehicles</button>
</div>

<style>
    .button-container {
        display: flex;
        justify-content: center;
        gap: 1rem;
        flex-wrap: wrap;
        padding: 20px 0 20px 0;
    }

    .narrative-choice {
        font-family: "Space Mono", monospace;
        background-color: #f0f0f0;
        border: 1px solid #ccc;
        padding: 0.5rem 1rem;
        cursor: pointer;
        flex: 1 1 100%;
        border-color: #FFFFFF #151515 #151515 #FFFFFF;
        border-style: solid;
    }

    .narrative-choice:hover {
        background-color: #e0e0e0;
    }

    .narrative-choice:active {
        background: #cccccc;
    }

    .narrative-choice.selected {
        background-color: #cccccc;
        border-color: #151515 #FFFFFF #FFFFFF #151515;
    }
    
    @media (min-width: 400px) {
        .narrative-choice {
            flex: 0 1 auto;
            max-width: none;
        }
    }
</style>