<script>
    import { scrollPositionStore } from "../../lib/stores.js";

    export let color;
    export let backgroundColor;
    export let backgroundY;

    let interval = null;
    let heading;
    let text;
    let windowHeight;
    let animationTriggered = false;

    const absoluteBackgroundY = $scrollPositionStore + backgroundY;
    $: relativeY = $scrollPositionStore - absoluteBackgroundY + windowHeight;

    const letters = "a b c d e f g h i j k l m n o p q r s t u v w x y z";

    // do this animation once
    $: if (relativeY < 100 && relativeY > 0 && !animationTriggered) {
        animationTriggered = true;

        let iteration = 0;

        const headingValue = heading?.innerText || '';
        const textValue = text?.innerText || '';
        const totalIterations = Math.max(headingValue.length, textValue.length);

        clearInterval(interval);

        interval = setInterval(() => {
            if (iteration <= totalIterations) {
                heading.innerText = headingValue
                    .split("")
                    .map((letter, index) => {
                        if (index < iteration) {
                            return headingValue[index];
                        }
                        return letters[Math.floor(Math.random() * letters.length)];
                    }).join("");

                text.innerText = textValue
                    .split("")
                    .map((letter, index) => {
                        if (index < iteration) {
                            return textValue[index];
                        }
                        return letters[Math.floor(Math.random() * letters.length)];
                    }).join("");

                if (iteration >= totalIterations) {
                    clearInterval(interval);
                }

                iteration += 1;
            }
        }, 3);
    }
</script>

<svelte:window bind:innerHeight={windowHeight} />

<div 
    class="annotation"
    style="color: {color};
    background-color: {backgroundColor};"
>
    <h1 bind:this={heading}><slot name="heading"></slot></h1>
    <p bind:this={text}><slot name="text"></slot></p>
</div>

<style>
    .annotation {
        position: relative;
        max-width: 400px;
        padding: 20px;
        left: 50%;
        transform: translateX(-50%);
    }
    p {
        font-family: "Space Mono", monospace;
        color: #fff;
        padding: 0 20px 0 20px;
    }
    h1 {
        font-family: "Space Mono", monospace;
        color: #fff;
        padding: 0 20px 20px 20px;
        margin: 0;
    }
</style>
